.hero {
  height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.portrait {
  border-radius: 100%;
  width: 20em;
}

.skills {
  padding-top: 50px;
  padding-bottom: 50px;
}

.skills-logo {
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px;
  border-radius: 15px;
}

.project-card {
  width: 100%;
  margin-bottom: 16px;
}

.project-tech {
  display: flex;
}

.technologies-logo-circle {
  height: 2.5rem;
  padding: 2px;
  margin: 2px;
}

@media screen and (max-width: 415px) {
  .portrait {
    border-radius: 15px;
    width: 100%;
  }
}

.social-icon a {
  border-radius: 100%;
  height: 50px;
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  font-size: 1.5em;
  margin: 5px;
  color: #000;
  text-decoration: none;
}